@import url(https://fonts.googleapis.com/css2?family=Piazzolla:ital,wght@1,100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Piazzolla:ital,wght@1,100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Piazzolla:ital,wght@1,100&display=swap);
body {
  margin: 0;
  font-family: "Piazzolla", serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.box {
  padding: 60px;
}

.bold {
  font-size: 2rem;
  font-weight: bold;
}

.chart {
  width: 50vw;
  height: 70vh;
  display: flex;
  align-self: flex-start;
  cursor: pointer;
  margin-left: 60px;
}
.heading {
  display: flex;
  padding: 30px;
}
.hero {
  margin-bottom: 160px;
}

@media only screen and (max-width: 900px) {
  .hero {
    flex-direction: column;
  }
  .chart {
    margin: 0 auto;
  }
  .heading {
    justify-content: center;
  }
}

.navbar {
  background-color: rgb(206, 233, 255);
}

.nav-link {
  padding: 0;
  font-size: 1.5rem;
  font-family: "Piazzolla", serif;
}
.nav-item {
  padding-left: 40px;
  padding-right: 40px;
}

.logo {
  font-family: "Piazzolla", serif;
}

.header {
  margin-bottom: 60px;
}

.school {
  cursor: pointer;
}
.school:hover {
  color: blue;
}

table {
  border-collapse: collapse;
  width: 100%;
}

td,
th {
  border: 1px solid #dddddd;
  text-align: left;
  padding: 8px;
}

tr:nth-child(even) {
  background-color: #dddddd;
}

.footer {
  background-color: rgb(41, 39, 39);
  padding: 100px 50px;
  font-family: "Piazzolla", serif;
  margin-top: 50px;
}

.footer-links {
  text-decoration: none;
  color: white;
  font-family: "Piazzolla", serif;
}
.footer-links:hover {
  text-decoration: none;
  color: rgb(160, 150, 150);
}

.copyright {
  height: 30px;
  background-color: rgb(31, 29, 29);
  font-family: sans-serif;
  padding-top: 5px;
}
@media only screen and (max-width: 767px) {
  .contact-container {
    margin-bottom: 40px;
  }
}

@media only screen and (max-width: 576px) {
  .social-container {
    margin-bottom: 40px;
  }
}

.student {
  cursor: pointer;
}
.student:hover {
  color: blue;
}

.collegePage {
  padding: 30px;
}

.title {
  font-size: 3rem;
  font-weight: bolder;
}

.bold {
  font-size: 2rem;
  font-weight: bold;
}



